<template>
	<view class="home">
		<view class="home-top">
			<swiper class="swiper" autoplay="true">
				<swiper-item>
					1
				</swiper-item>
				<swiper-item>
					2
				</swiper-item>
				<swiper-item>
					3
				</swiper-item>
			</swiper>
		</view>
		<u--input class="home-input" placeholder="搜索" prefixIcon="search" prefixIconStyle="font-size: 22px;color: #909399"></u--input>
		<view class="">
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				show: false,
				value1: Number(new Date()),
				list1: [{
					name: '关注',
				}, {
					name: '推荐',
				}, {
					name: '电影'
				}, {
					name: '科技'
				}, {
					name: '音乐'
				}, {
					name: '美食'
				}, {
					name: '文化'
				}, {
					name: '财经'
				}, {
					name: '手工'
				}]
			}
		},
		onLoad() {},
		filters: {
			transTime: function(val) {
				var date = new Date();
				date.setTime(val);
				var month = date.getMonth() + 1;
				var hours = date.getHours();
				if (hours < 10)
					hours = "0" + hours;
				var minutes = date.getMinutes();
				if (minutes < 10)
					minutes = "0" + minutes;
				var time = date.getFullYear() + "-" + month + "-" + date.getDate() +
					" " + hours + ":" + minutes;
				return time;

			}
		},
		methods: {
			close() {
				console.log(this.value1);
				this.show = !this.show
			}
		}
	}
</script>

<style lang="scss">
	.home {
